	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/3/the-shawshank-redemption-a-powerful-lesson/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>The simple but powerful message is this: ‘Enjoy The Journey’. Remember the smile on Andy’s face when he disposes off the pieces from the wall every day in that prison ground? It could be the light at the end of the tunnel that keeps you going, or it could be the thrill that comes from the stealthiness of it all – but you have to remember that the journey is more important and satisfying than the end destination – getting into that mode is absolutely essential for everyone embarking on the journey.</p>\n<p>You have no idea how deep the wall is. You have nothing but a rock hammer. You have no idea when you will be caught. But what you do have, is the journey. Enjoy the journey.</p>\n</blockquote>","frontmatter":{"title":"'The Shawshank Redemption': A powerful lesson","date":"March 04, 2013","passthroughUrl":"http://statspotting.com/a-lesson-from-the-shawshank-redemption/"}}},"pathContext":{"slug":"/blog/2013/3/the-shawshank-redemption-a-powerful-lesson/","previous":{"fields":{"slug":"/blog/2013/3/hackers-a-hacker-news-app-for-iphone/"},"frontmatter":{"title":"Hackers - a Hacker News app for iPhone","passthroughUrl":null,"type":null}},"next":{"fields":{"slug":"/blog/2013/3/apples-reality-distortion-field-relocates-to-wall-street/"},"frontmatter":{"title":"Apple’s Reality Distortion Field relocates to Wall Street","passthroughUrl":"http://techland.time.com/2013/03/04/apples-reality-distortion-field-relocates-to-wall-street/","type":null}}}}

